:root {
  //--navColor: #2F4550;
  --navHeight: 56px;
  //--primaryColor: #f09d51;
  //--mainWhite: #fff;
  //--mainBlack: #222;
  //--mainGrey: #ececec;
  //--mainSpacing: 0.1rem;
  --mainTransition: all 0.3s linear;
  --galleryBoxWidth: 120px;
}

// It seems that $sass variables are not nestable, so stick to var(--navHeight)'s
$navHeight:56px;

@media only screen and (min-width: 601px){
	:root {
	  --navHeight: 64px;
	  --galleryBoxWidth: 220px;
	}	
	$navHeight:64px;

	nav, nav .nav-wrapper i, nav a.sidenav-trigger, nav a.sidenav-trigger i {
		line-height: var(--navHeight);
	}		

	.cart{
		top:var(--navHeight);
	}
	
	#Display .gallery-box{
		 width:220px; 
		 height:220px; 
	}
}

* {padding:0; margin:0; box-sizing: border-box; }

html {
	height:100vh;
	width:100vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	font-size:1.5rem;
	background:darkcyan;
	background:cyan;
	height:100vh;
	max-width:100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  width:100vw
}




#Navbar {
  position: sticky;
  //position: fixed;
  top: 0;
	z-index: 3;
	
  nav{
  
	  //box-shadow:0 8px 17px 2px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);
  
    .container > ul > span > i{
      height: var(--navHeight);
      width: var(--navHeight);
      line-height: var(--navHeight);
      text-align: center;
    }
    .container > ul > span:hover{
      background-color: rgba(0,0,0,0.1);
    }
    .brand-logo {
      left: inherit ! important; 
      -webkit-transform: translateX(0) ! important; 
      transform: translateX(0) ! important;
      font-size:20px;
    }
    i {
			font-size:20px;
		}
  }  

  .search{
    height:0;
    // position:sticky;
    top:var(--navHeight);
    transition:height .2s linear;
    overflow:hidden;
    z-index:2;
    width: 100%;
  }

  .search-open{
    height:calc(var(--navHeight) + 1.5rem);
  }

  .search-bar {
    height:var(--navHeight);
    height:calc(var(--navHeight) + 1.5rem);
    position:sticky;
    top:var(--navHeight);
    z-index:2;
    display: grid;
    align-items: center;
    transition:height .2s;

    .container {
      background:white;
      height:3rem;
      border-radius:5px;
      position:relative;
    }
    
    .container form{
      padding:0
    }
    
    .container form button{
      padding:0;
      margin:0;
      position:absolute;
      top:0;
      right:0;
      height: 100%;
      width: var(--navHeight);
      text-align: center;
      background:none;
      border:none;
    }
    
    .container input, .searchbar-input{
      font-size:1.5rem ! important;
      padding-left:1rem ! important;
      box-sizing:border-box;
      border:none  ! important;
    }
      
    .container button i{
      font-size: 2rem;
    }

  }

}

#Display {
	position:relative;
	top: var(--navHeight);
	top: 0;
	text-align:center;
	padding-top: 10px;
	padding-bottom: 30px;
	// To push footer to bottom when Display content < 100vh.
	min-height: calc(100vh - var(--navHeight) * 2);

	.gallery-box{
		 width:var(--galleryBoxWidth);
		 height:var(--galleryBoxWidth);
		 border:1px solid grey; 
		 display:inline-block; 
		 margin-top:1.01rem; 
		 margin-right:1.5rem;		 
		 margin-top:.01rem; 
		 margin-right:.5rem;
		 //box-shadow:0 8px 17px 2px rgba(0,0,0,0.14),0 3px 14px 2px rgba(0,0,0,0.12),0 5px 5px -3px rgba(0,0,0,0.2);
	}

}

#Footer {
  position: relative;
  top: var(--navHeight);
  top: 0;
  width: 100%;
  color: white;
  padding-top: 0;
  font-size: 1.2rem;

  .container{
    line-height: var(--navHeight);  
    
    div{
      display:inline-block;
      //width: max-content;
      //margin:0px 20px;
      margin-left:0
    }

    img{
      width:36px;
      height:26px;
      vertical-align: middle;
    }
  }


}


.container{width:90% ! important;}


#tester{
	text-align:left;
	button{
		padding:10px;
		margin-bottom:10px;
	}
	.readout{
		padding:10px;
		border:2px solid grey;
		min-height: 50px;
		text-align:left;
	}

}


